@import '../../theme.scss';

section.section {
	&.section-placeholder {
		min-height: 300px;
		margin: auto;
	}
	&:nth-of-type(even) {
		background-color: $iok-lightgrey;
	}
	&:nth-of-type(odd) {
		background-color: $bg-secondary;
	}
	padding: 140px 0;
	@include mobile {
		padding: 70px 0;
	}
}